/**
 * 工具类
 */
export default {
  // 播放数量处理
  _tranNumber(num, point) {
    //去掉小数点
    let numStr = num.toString().split(".")[0];
    if (numStr.length < 6) {
      //十万以内
      return numStr;
    } else if (numStr.length >= 6 && numStr.length <= 8) {
      let decimal = numStr.substring(
        numStr.length - 4,
        numStr.length - 4 + point
      );
      return parseFloat(parseInt(num / 10000) + "." + decimal) + "万";
    } else if (numStr.length > 8) {
      let decimal = numStr.substring(
        numStr.length - 8,
        numStr.length - 8 + point
      );
      return parseFloat(parseInt(num / 10000000) + "." + decimal) + "亿";
    }
  },

  // 格式化日期
  _tranDate(str) {
    let date = new Date(str);
    let year = date.getFullYear();
    let mouths = date.getMonth() + 1;
    let day = date.getDate();
    mouths = mouths < 10 ? `0${mouths}` : mouths;
    day = day < 10 ? `0${day}` : day;
    return `${year}-${mouths}-${day}`;
  },

  // 数据字符串化
  _arrToString(arr) {
    let str = "";
    arr.forEach(e => {
      str += e.id + ",";
    });
    str = str.slice(0, str.length - 1);
    return str;
  },

  // 时长转换
  _msTos(ms) {
    let min = Math.floor(ms / 60000);
    let sec = Math.ceil(ms % 60000 / 1000);
    sec = sec > 10 ? sec : "0" + sec;
    return `${min}分${sec}秒`
  },

  // 解析歌词
  _parseLyric(slyric) {
    let line = slyric.toString().split('\n')//截取每一行歌曲信息(已换行为界限)line为数组
    let _lrcList=[] //空数组，目的是为了存放数据
    //对每一行进行解析
    line.forEach((elem) => {
      //提取初步时间
      let time = elem.match(/\[(\d{2,}):(\d{2})(?:\.(\d{2,3}))?]/g)
      if (time != null) {
        //取出歌词
        let lrc = elem.split(time)[1]
        //对时间进行优化
        let timeReg = time[0].match(/(\d{2,}):(\d{2})(?:\.(\d{2,3}))?/)
        //将时间转化成秒数 数组timeGeg中[0]为总时间 [1]为分钟数 [2]为秒数 [3]为毫秒数
        let time2Seconds = parseInt(timeReg[1]) * 60 + parseInt(timeReg[2])+ parseInt(timeReg[3])/1000
        //console.log(time2Seconds)
        _lrcList.push({
          lrc,
          time:time2Seconds,
        })
      }
    })
    return _lrcList;
  }
}